@import url(https://fonts.googleapis.com/css?family=Patrick+Hand&display=block);
body {
  margin: 0;
  font-family: "Patrick Hand", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Main-container{display:grid;grid-template-areas:"nav nav" "main main";grid-template-columns:auto;grid-template-rows:minmax(100px, 120px) 1fr;height:100vh;height:calc(1vh * 100);height:calc(var(--vh, 1vh) * 100)}.Main-container nav{grid-area:nav}.Main-container main{grid-area:main}@media (min-width: 576px){.Main-container{grid-template-rows:100px 1fr}}@media (min-width: 992px){.Main-container{grid-template-areas:"nav main";grid-template-columns:minmax(200px, 300px) minmax(600px, 1fr);grid-template-rows:auto}}.Nav-container{background:#dc7373;position:relative;display:flex;justify-content:center;align-content:center;flex-wrap:wrap}@media (min-width: 992px){.Nav-container{padding:0 1.1em;flex-direction:column;justify-content:start}}.Nav-container .Nav-title{text-transform:capitalize;text-align:center;font-size:clamp(1.9em, 3vw, 4em);align-self:center;margin:0;width:100%}@media (min-width: 576px){.Nav-container .Nav-title{width:50%;text-align:start}}@media (min-width: 992px){.Nav-container .Nav-title{width:auto;padding:1em 0;text-align:center}}.Nav-container .Nav-desc{display:none;padding:1.2em}@media (min-width: 992px){.Nav-container .Nav-desc{display:block;text-align:justify}}.Nav-container .Nav-controls{display:flex;flex-direction:row;justify-content:center;position:relative}@media (max-width: 576px){.Nav-container .Nav-controls{order:1;width:100%;padding:0.5em}}@media (min-width: 576px){.Nav-container .Nav-controls{align-items:center;flex-wrap:wrap}}@media (min-width: 992px){.Nav-container .Nav-controls{margin-top:1.5em;width:100%;flex-wrap:nowrap}}.Nav-container .Nav-settings-container{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:1.2em;margin:1.2em 0;background:#63bbde;border-radius:0.5em;position:absolute;top:2.4em;box-shadow:0px 2px 0px 4px #000, 0 2px 0px 2px #e0f7fa}.Nav-container .Nav-settings-container .Nav-setting{display:flex;width:100%;justify-content:center;align-items:center;margin:0.2em 0.6em}.Nav-container .Nav-settings-container .Nav-setting label{width:50%}.Grid-container{display:flex;justify-content:center;align-items:center;overflow:hidden;background:#212121}.Grid-container .Grid{display:grid}.btn{position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;cursor:pointer;text-align:center;font:normal 1em "Patrick Hand", sans-serif;margin:0 0.4em;margin-right:10px;text-transform:uppercase;color:#000;text-decoration:none;padding-bottom:2px;border-radius:5px;box-shadow:0 2px 0 #000;transition:padding 0.1s, box-shadow 0.1s, top 0.1s;background-image:url("data:image/gif;base64,")}.btn:hover{box-shadow:0 2px 0 #000, 0 2px 0px 2px #dc7373cc}.btn:active{top:4px;padding-bottom:0px;box-shadow:0 1px 0 #000}.btn span{background:#fff;display:block;padding:5px 15px;border-radius:5px;border:2px solid #000}.btn input{background:#fff;display:block;padding:5px 14px;border-radius:5px;border:2px solid #000}

